<template>
    <v-container>
        <v-row>
            <v-col
                cols="9"
                id="content"
            >
                <h1>{{ article.title }}</h1>
                <p>{{ article.content }}</p>
            </v-col>
            <v-col
                cols="3"
            >
                <introduction/>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import api from '../services/api'
import Introduction from '../components/Introduction'

export default {
    components: {
        Introduction,
    },

    data() {
        return {
            article: {}
        }
    },

    methods: {
        getArticle() {
            api.get('/blog/' + this.$route.params.id)
            .then(response => {
                this.article = response.data
            })
        }
    },

    created () {
        this.getArticle()
    }
}
</script>

