import Vue from 'vue'
import VueRouter from 'vue-router'
import TopPage from './pages/TopPage'
import Detail from './pages/Detail'

Vue.use(VueRouter)

const router = new VueRouter({
    mode: 'history',
    routes: [
        {
            path: '/',
            component: TopPage
        },
        {
            path: '/article/:id',
            component: Detail,
            name: 'detail'
        }
    ]
})

export default router